.IShopProductsList {
    width: fit-content;
    padding: 7px;
  }
  
  .IShopProductsList .IShopTitle {
    font-weight: bold;
    margin-bottom: 7px;
  }
  
  .IShopProductsList .IShopInfo {
    border-collapse: collapse;
    border: 2px solid grey;
    
  }
  
  /* .IShopProductsList .IShopInfo > tbody > tr > td, */
  .IShopProductsList .IShopInfo > thead > tr > th {
    border: 1px solid #000;
    padding: 5px;
    text-align: center;
  }

  .IShopContent {
    background-color: white;
  }
  
  .IShopProductsList .TableHeader {
    background-color: #f0f0f0;
  }